import fg from 'fast-glob'
import fsa from 'fs-extra'
import * as core from "@actions/core";
import * as exec from "@actions/exec";
import * as gh from "@actions/github"
import { join } from 'node:path'
import { createPullRequest } from 'octokit-plugin-create-pull-request'

async function run() {
  try {
    const token = core.getInput("myToken", { required: true });
    const isDebug = core.getBooleanInput("debug")

    const apps = await getApps(process.cwd(), isDebug)
    const context = gh.context;
    const octokit = gh.getOctokit(token, {}, createPullRequest)
    // @ts-expect-error createPullRequest exists
    const createPR = octokit.createPullRequest as ReturnType<typeof createPullRequest>['createPullRequest']
    const createLogger = (appName: string) => (msg: string) => core.info(`${appName}: ${msg}`)

    for await (const app of apps) {
      const logger = createLogger(app.name)
      logger(`Checking for updates...`)
      const cloneDir = `/tmp/${app.name}`
      await exec.exec(`git clone ${app.repo} ${cloneDir}`)
      let fullSha = ""
      await exec.exec(`git -C ${cloneDir} log -1 --format=%H`, [], {
        listeners: {
          stdout: (data: Buffer) => {
            fullSha += data.toString()
          }
        }
      })
      fullSha = fullSha.trim()

      const metaPath = join(app.dir, 'meta.json')
      let meta = app
      let version = ""

      if (app.type === 'version') {
        const packageJson = (await fsa.readJSON(join(cloneDir, app.file)))?.version
        if (packageJson !== app.version) {
          logger(`Found new version ${packageJson}`)
          meta = { ...app, sha: fullSha, version: packageJson }
          version = packageJson
        }
      } else if (app.type === 'sha') {
        if (fullSha !== app.sha) {
          let shortSha = ""
          await exec.exec(`git -C ${cloneDir} log -1 --format=%h`, [], {
            listeners: {
              stdout: (data: Buffer) => {
                shortSha += data.toString()
              }
            }
          })
          shortSha = shortSha.trim()

          logger(`Found new commit ${fullSha}`)
          meta = { ...app, sha: fullSha }
          version = shortSha
        }
      }

      const params: Parameters<typeof createPR>[0] = {
        owner: context.repo.owner,
        repo: context.repo.repo,
        title: `chore(${app.name}): update sha to ${version}`,
        head: `${app.name}-${version}`,
        base: 'master',
        body: `\`\`\`json
    ${JSON.stringify({
      app: app.name,
      version,
      dir: app.dir,
      message: `Auto-generated PR to update ${app.name} sha to ${version}`
    }, null, 2)}
    \`\`\``,
        changes: [
          {
        files: {
          [metaPath]: {
            content: JSON.stringify(meta, null, 2),
            encoding: 'utf-8'
          }
        },
        commit: `chore(${app.name}): update sha to ${version}`
          }
        ]
      }

      isDebug && logger(`PR params:`)
      isDebug && logger(JSON.stringify(params, null, 2))

      const result = await createPR(params)
      result && logger(`PR created: ${result.data.html_url}`)
    }

    core.setOutput("status", "success");
  } catch (error) {
    if (error instanceof Error) {
      core.setFailed(error.message);
    } else {
      core.setFailed("An unexpected error occurred");
    }
  }
}

run();

//  apps  app 
export async function getApps(cwd: string = process.cwd(), isDebug = false) {
  const appsDir = join(cwd, 'apps')
  if (!fsa.existsSync(appsDir)) {
    return []
  }

  const dirs = await fg.glob('apps/*', { onlyDirectories: true, deep: 1, cwd: process.cwd(), absolute: false })
  isDebug && console.log(dirs)

  const results = dirs.map((dir) => {
    const metaPath = join(dir, 'meta.json')
    if (!fsa.existsSync(metaPath)) {
      return null
    }
    const meta = fsa.readJsonSync(join(dir, 'meta.json'))
    return { ...meta, dir }
  })

  isDebug && console.log(results)

  return results.filter(Boolean)
}
